import React, { useState } from "react"
import * as yup from 'yup';
import { Formik } from 'formik'
import axios from 'axios'
import Layout from "../components/layout/layout"
import Metadata from "../components/metadata/metadata"
import Row from "react-bootstrap/Row"
import Col from "react-bootstrap/Col"
import Card from "../components/contact/ContactCard"
//styles
import contactStyle from "../components/contact/contact.module.css"
import InnerForm from "../components/contact/InnerForm";

const serverEndpoint = "https://quasiscience.com/charity-contacts/"
const expression = new RegExp(/^(("[\w-\s]+")|([\w-]+(?:\.[\w-]+)*)|("[\w-\s]+")([\w-]+(?:\.[\w-]+)*))(@((?:[\w-]+\.)*\w[\w-]{0,66})\.([a-z]{2,6}(?:\.[a-z]{2})?)$)|(@\[?((25[0-5]\.|2[0-4][0-9]\.|1[0-9]{2}\.|[0-9]{1,2}\.))((25[0-5]|2[0-4][0-9]|1[0-9]{2}|[0-9]{1,2})\.){2}(25[0-5]|2[0-4][0-9]|1[0-9]{2}|[0-9]{1,2})\]?$)/i);

const schema = yup.object().shape({
  name: yup.string().required(),
  email: yup.string().email().required(),
  message: yup.string().required()
});

const Contact = () => {
  const [cardAnimation, setCardAnimation] = React.useState(0);
  setTimeout(function () {
    setCardAnimation(1);
  }, 500);
  let [values, setValues] = useState({
    name: "",
    email: "",
    message: "",
  });
  let [errors, setErrors] = useState({
    name: false,
    email: false,
    message: false,
  });
  let [touched, setTouched] = useState({
    name: false,
    email: false,
    message: false,
  });

  let [buttonText, setButtonText] = useState("Send");

  const onChangeHandler = (event) => {
    setValues((previousState) => ({
      ...previousState,
      [event.target.id]: event.target.value
    }));
    console.log(values);
  };

  const ValidateInputs = () => {
    let isEmailValid = true;
    let isMessageValid = true;
    if (!expression.test(String(values.email).toLowerCase())) {
      setErrors((previousState) => ({
        ...previousState,
        "email": true,
      }));
      isEmailValid = false;
    } else {
      setErrors((previousState) => ({
        ...previousState,
        "email": false,
      }));
    };
    if (values.message.length <= 50 || values.message.length >= 800) {
      setErrors((previousState) => ({
        ...previousState,
        "message": true
      }));
      isMessageValid = false;
    } else {
      setErrors((previousState) => ({
        ...previousState,
        "message": false
      }));
    };
    if (isEmailValid && isMessageValid) {
      return true
    } else {
      return false;
    };
  };

  const onSubmitHandler = (event) => {
    event.preventDefault();
    const correctInputs = ValidateInputs()
    if (correctInputs) {
      let info_to_send = {
        ...values,
        website: "hopelabs.co.uk"
      };
      setButtonText("... Sending ...");
      axios({
        method: "post",
        url: serverEndpoint,
        data: info_to_send,
        headers: { 'Content-Type': 'application/json' }
      }).then((response) => {
        if (response.status === 200) {
          setButtonText("Message Sent");
          setTimeout(function () {
            setButtonText("Send");
          }, 1000);
          // alert("Message successfully delivered.")
          setValues({
            ...values,
            name: "",
            email: "",
            message: "",
          });
          console.log(response);
        } else if (response.status !== 200) {
          alert("Message failed to deliver.")
          console.log(response);
        };
      }).catch((error) => {
        alert("Error occurred.")
        console.error('There was an error!', error);
      });
    } else {
      console.log("Form contains error. Do not send email.");
    };
  };

  const onBlurHandle = () =>{
    return(null)
  };

  return (
    <Layout>
      <Metadata
        title="Contact Us"
        description="We encourage you to get in touch if you have any questions or to share your feedback."
      />
      <div className={`${contactStyle.container} ${contactStyle.verticalPosition}`}>
          <Row>
            <Col className={contactStyle.centerCol} lg={9}>
              <Card className={`${contactStyle.card}${cardAnimation === 1? "" : contactStyle.cardHidden}`}>
                <Formik 
                  validationSchema={schema} 
                  onSubmit={onSubmitHandler} 
                  initialValues ={{
                    name:"",
                    email:"",
                    message:""
                  }}
                >
                  {({onSubmitHandler, onChangeHandler, onBlurHandle, values, touched, isValid, errors}) =>(
                    <InnerForm 
                      endpoint={serverEndpoint}
                      handleSubmit={onSubmitHandler}
                      handleChange={onChangeHandler}
                      handleBlur={onBlurHandle}
                      state={values}
                      buttonState={buttonText}
                    />
                  )}
                </Formik>
              </Card>
            </Col>
          </Row>
      </div>
    </Layout>
  )
}

export default Contact
